.header
  position: fixed
  top: 0
  left: 0
  width: 100%
  z-index: 20
  background-color: #fff
  border-bottom: 1px solid transparent
  transition: border-color .3s
  &__top
    background: $main-gradient
    .container
      padding: 20px 15px
      position: relative
      transition: padding .3s
      @media (max-width: 360px)
        padding: 20px 5px
      @media (min-width: 1200px)
        padding: 16px
        max-width: 1410px
    &__mobile
      @media (min-width: 576px)
        text-align: center
      @media (min-width: 1200px)
        display: none
      .header-link
        display: block
        background-color: #fff
        border-radius: 100px
        padding: 10px
        font-weight: 700
        font-size: 11px
        line-height: 20px
        text-align: center
        letter-spacing: .35px
        text-transform: uppercase
        color: $accent-color
        transition: background-color .3s, color .3s
        @media (max-width: 360px)
          font-size: 10px
        @media (min-width: 576px)
          display: inline-block
          padding: 10px 25px
        @media (min-width: 768px)
          font-size: 14px
        &:hover
          background-color: $accent-color
          color: #fff
    &__desk
      @media (min-width: 1200px)
        display: block !important
        text-align: center
        .header-link
          text-transform: uppercase
          color: #000
          font-weight: 700
          font-size: 15px
          letter-spacing: .05em
          &__txt
            span
              text-decoration: underline
          &__span
            display: inline-block
            margin-left: 30px
            background-color: #fff
            color: $accent-color
            border-radius: 100px
            padding: 10px 24px
            transition: background-color .3s, color .3s
            font-size: 12px
            letter-spacing: .35px
            &:hover
              background-color: $accent-color
              color: #fff
    .header-close
      position: absolute
      top: 3px
      right: 3px
      font-size: 16px
      line-height: 16px
      color: rgba($txt-color, .5)
      transition: color .3s
      z-index: 1
      @media (min-width: 576px)
        top: 10px
        right: 10px
      @media (min-width: 768px)
        font-size: 20px
        line-height: 20px
      @media (min-width: 1200px)
        font-size: 22px
        line-height: 22px
        top: calc(50% - 12px)
        right: 20px
      &:hover
        color: $txt-color
  &__main
    padding: 10px 0
    transition: padding .4s
    @media (min-width: 1200px)
      padding: 17px 0
    .container
      @media (min-width: 1200px)
        max-width: 1470px
    .mmenu-wrapper
      display: flex
      align-items: center
      justify-content: flex-start
      .logo-link
        display: inline-block
        flex: 0 0 auto
        width: auto
        .logo-img
          display: block
          width: auto
          max-width: 100%
          @media (min-width: 1200px)
            width: 105px
            position: relative
            left: -3px
      .mmenu
        background-color: #fff
        transition: transform .3s
        overflow-y: scroll
        top: 100%
        left: 0
        width: 100%
        height: calc(100vh - var(--header-height) + 1px)
        border-top: 1px solid #eee
        @media (min-width: 640px)
          max-width: 400px
          left: auto
          right: 0
          z-index: 2
        @media (min-width: 1200px)
          position: relative !important
          transform: none !important
          top: 0
          left: 0
          width: auto
          height: auto
          margin: 0 0 0 20px
          border: none
          overflow: visible
        @media (min-width: 1300px)
          margin: 0 0 0 60px
        .mmenu-list
          @media (min-width: 1200px)
            display: flex
            align-items: center
            justify-content: flex-start
          &__item
            display: flex
            align-items: center
            justify-content: flex-start
            flex-wrap: wrap
            padding: 5px 15px
            border-bottom: 1px solid #eee
            @media (min-width: 1200px)
              flex: 0 0 auto
              width: auto
              border: none
              padding: 0
              margin: 0 15px 0 0
              position: relative
            @media (min-width: 1300px)
              margin: 0 30px 0 0
            &.active
              .mmenu-list__sublink
                color: $accent-color
                transform: scale(1,-1)
            &:hover
              @media (min-width: 1200px)
                .mmenu-list__link
                  color: $accent-color
                .mmenu-list__sublink
                  color: $accent-color
                .submenu-list
                  display: block !important
          &__link
            display: inline-block
            padding: 10px 0
            font-weight: 700
            font-size: 14px
            line-height: 20px
            color: #000
            text-transform: uppercase
            transition: color .3s
            @media (min-width: 1200px)
              font-size: 12px
              letter-spacing: .55px
            &:hover
              color: $accent-color
          &__sublink
            display: inline-block
            margin: 0 0 0 auto
            padding: 5px
            font-size: 14px
            line-height: 14px
            color: #c4c4c4
            @media (min-width: 1200px)
              font-size: 12px
              line-height: 12px
          .submenu-list
            flex: 0 0 100%
            width: 100%
            padding: 0 15px 15px
            @media (min-width: 1200px)
              position: absolute
              top: 100%
              left: 0
              width: 300px
              background-color: #fff
              border: 1px solid #eee
              border-radius: 5px
            &__item
              display: block
              width: 100%
              margin-top: 10px
            &__link
              display: inline-block
              font-size: 15px
              line-height: 20px
              color: $txt-color
              position: relative
              &:before
                content: ''
                display: block
                width: 0
                height: 1px
                background-color: $accent-color
                position: absolute
                bottom: 0
                left: 0
                transition: width .3s
              &:hover
                &:before
                  width: 100%

        .personal-block
          display: flex
          align-items: center
          justify-content: center
          flex-wrap: wrap
          padding: 30px 0
          &__item
            display: block
            flex: 0 0 auto
            width: auto
            margin: 0 auto
            padding: 15px 25px
            border: 2px solid $accent-color
            border-radius: 100px
            font-weight: 700
            font-size: 14px
            line-height: normal
            text-align: center
            transition: background-color .3s, color .3s
            &.enter-link
              background-color: $accent-color
              color: #fff
              &:hover
                background-color: $accent-darkness-color
                border-color: $accent-darkness-color
            &.sing-in-link
              color: $txt-color
              &:hover
                background-color: $accent-color
                color: #fff

        &.show
          transform: translateX(0) !important
      .profile-links
        display: flex
        align-items: center
        justify-content: flex-start
        flex: 0 0 auto
        width: auto
        margin: 0 25px 0 auto
        @media (min-width: 1200px)
          margin: 0 0 0 auto
        &__item
          flex: 0 0 auto
          width: auto
          display: inline-block
          margin-left: 20px
          @media (min-width: 1200px)
            margin-left: 18px
          &.support-item
            font-size: 28px
            line-height: 28px
            color: #000
            position: relative
            top: -2px
          &.cart-item
            font-size: 28px
            line-height: 28px
            color: #000
            position: relative
            .cart-count
              display: flex
              align-items: center
              justify-content: center
              min-width: 18px
              height: 18px
              position: absolute
              top: -5px
              right: -5px
              border-radius: 50%
              background-color: $accent-color
              color: #fff
              font-size: 12px
              line-height: 12px
        .personal-block
          display: flex
          align-items: center
          justify-content: center
          flex-wrap: wrap
          margin: 0 0 0 20px
          &__item
            display: inline-block
            flex: 0 0 auto
            width: auto
            margin: 0 auto
            padding: 14px 22px
            border: 2px solid $accent-color
            border-radius: 100px
            font-weight: 700
            font-size: 12px
            line-height: normal
            text-align: center
            transition: background-color .3s, color .3s
            &.enter-link
              background-color: $accent-color
              color: #fff
              text-transform: uppercase
              &:hover
                background-color: $accent-darkness-color
                border-color: $accent-darkness-color
            &.sing-in-link
              color: $accent-color
              margin-left: 28px
              &:hover
                background-color: $accent-color
                color: #fff




      @import "hamburger"
  &.scroll
    border-bottom: 1px solid #eee
    .header__main
      padding: 0
      .mmenu
        height: calc(100vh - 100%)