@import "settings"
@import "_mixins/_mixins"
@import "_fonts/fonts_list"
@import "_fonts/fontello"                                                                                               // if you use fontello fonts
@import "_components/reset"
@import "_components/header"

/* main page */
.main-block
  padding: 10px 0
  @media (min-width: 1200px)
    padding: 15px 0
    position: relative
    z-index: 2
  &__title
    font-family: Veles, sans-serif
    font-weight: 700
    font-size: 20px
    line-height: 32px
    letter-spacing: 0
    @media (min-width: 576px)
      font-size: 25px
      line-height: 40px
    @media (min-width: 768px)
      font-size: 28px
      line-height: 50px
    @media (min-width: 960px)
      font-size: 32px
      line-height: 50px
      max-width: 700px
    @media (min-width: 1200px)
      max-width: 650px
      font-size: 30px
  &__content
    position: relative
    padding-top: 50px
    display: flex
    flex-direction: column-reverse
    align-items: flex-start
    justify-content: flex-start
    flex-wrap: wrap
    @media (min-width: 576px)
      padding-top: 75px
    @media (min-width: 768px)
      flex-direction: row
      padding-top: 20px
    &__left-side
      width: 100%
      @media (min-width: 768px)
        flex: 0 0 300px
        max-width: 300px
      @media (min-width: 768px)
        flex: 0 0 350px
        max-width: 350px
      @media (min-width: 1200px)
        flex: 0 0 330px
        max-width: 330px
      .main-btn
        display: block
        width: 100%
        max-width: 252px
        position: absolute
        top: 15px
        left: 0
        padding: 13px
        background-color: $accent-color
        border-radius: 100px
        color: #fff
        font-weight: 700
        font-size: 12px
        line-height: 22px
        text-align: center
        text-transform: uppercase
        @media (min-width: 576px)
          top: 25px
        @media (min-width: 768px)
          position: relative
          top: 0
        @media (min-width: 1200px)
          max-width: 225px
      .main-list
        margin-top: 45px
        @media (min-width: 768px)
          margin-top: 35px
        @media (min-width: 1200px)
          margin-top: 40px
        ul
          margin: 0
          padding: 0
          list-style-type: none
          @media (min-width: 768px)
            font-size: 15px
          @media (min-width: 960px)
            font-size: 16px
          li
            display: block
            width: 100%
            padding-left: 35px
            position: relative
            margin-top: 22px
            @media (min-width: 768px)
              margin-top: 15px
            @media (min-width: 1200px)
              margin-top: 21px
            &:nth-of-type(1)
              margin-top: 0
            &:before
              content: '\e805'
              font-family: Fontello
              font-style: normal
              font-weight: normal
              speak: never
              display: block
              margin: 0
              text-decoration: inherit
              text-align: center
              font-variant: normal
              text-transform: none
              font-size: 18px
              line-height: 18px
              color: $accent-color
              position: absolute
              top: 1px
              left: 2px
      .main-social
        display: flex
        align-items: flex-start
        justify-content: flex-start
        flex-wrap: wrap
        margin-top: 55px
        font-weight: 700
        max-width: 365px
        @media (min-width: 576px)
          max-width: 450px
        @media (min-width: 768px)
          max-width: 320px
          width: 320px
          margin-top: 35px
        @media (min-width: 1200px)
          margin-top: 85px
        .social-title
          display: block
          width: 100%
          font-weight: 700
          color: $accent-color
          margin-bottom: 6px
          @media (min-width: 576px)
            margin-bottom: 10px
          @media (min-width: 1200px)
            white-space: nowrap
            font-size: 14px
            letter-spacing: .35px
            margin-bottom: 7px
        .social-full, .social-youtube, .social-tg
          display: inline-flex
          align-items: center
          justify-content: flex-start
          font-size: 20px
          line-height: 20px
          color: #000
          letter-spacing: .25px
        .social-full
          flex: 0 0 100%
          width: auto
          margin-bottom: 18px
          span
            display: inline-block
            border: 2px solid $txt-color
            border-radius: 100px
            color: $txt-color
            font-size: 10px
            line-height: 10px
            text-transform: uppercase
            margin-left: 5px
            padding: 10px 60px
            transition: border-color .3s, color .3s, background-color .3s
            @media (max-width: 360px)
              padding: 10px 30px
            @media (min-width: 576px)
              margin-left: 15px
              padding: 10px 40px
            @media (min-width: 1200px)
              margin-left: 7px
              padding: 10px 17px
            &:hover
              background-color: $accent-color
              border-color: $accent-color
              color: #fff
        .social-youtube
          flex: 0 0 auto
          width: auto
          .icon
            color: #FF0000
            font-size: 32px
            line-height: 20px
            margin-right: 5px
        .social-tg
          flex: 0 0 auto
          width: auto
          margin: 0 0 0 60px
          @media (min-width: 576px)
            margin: 0 0 0 30px
          .icon
            color: #0088cc
            font-size: 28px
            line-height: 20px
            margin-right: 8px
    &__right-side
      width: calc(100% + 30px)
      margin: 35px -15px 0
      position: relative
      padding: 0 15px 20px
      overflow: hidden
      @media (min-width: 576px)
        width: 100%
        max-width: 475px
        margin: 15px auto 0
      @media (min-width: 768px)
        max-width: 50vw
        flex: 0 0 50vw
        padding: 0 20px 0 0
      @media (min-width: 1024px)
        max-width: 55vw
        flex: 0 0 55vw
      @media (min-width: 1200px)
        max-width: 742px
        flex: 0 0 742px
        margin: -55px 0 0 auto
        padding: 0
        position: relative
        right: -15px
      .main-slider
        @media (min-width: 768px)
          height: 40vw
        @media (min-width: 1200px)
          height: 510px
        &.swiper-vertical
          .swiper-wrapper
            flex-direction: column
          .swiper-slide
            flex: 0 0 auto
            height: auto
            width: auto
        .slider-item
          position: relative
          .slide-image
            display: block
            width: auto
            max-width: 100%
            margin: 0
          .slide-link
            display: flex
            align-items: center
            justify-content: center
            width: 100%
            height: 100%
            position: absolute
            top: 0
            left: 0
            &:after
              display: flex
              align-items: center
              justify-content: center
              width: 50px
              height: 50px
              background-color: #fff
              border-radius: 50%
              top: calc(50% - 25px)
              left: calc(50% - 25px)
              font-family: Fontello
              font-style: normal
              font-weight: normal
              speak: never
              text-decoration: inherit
              text-align: center
              font-variant: normal
              text-transform: none
              color: $accent-color
              font-size: 20px
              line-height: 20px
              animation: 3s infinite alternate scaleIcon
            &.js-video-link, &.js-image-link
              &:before
                content: ''
                display: block
                width: 50px
                height: 50px
                border-radius: 50%
                position: absolute
                top: calc(50% - 25px)
                left: calc(50% - 25px)
                border: 2px solid $accent-color
                animation: 3s infinite linear my_zoom
            &.js-video-link
              &:after
                content: '\e80b'
                padding-left: 4px
            &.js-image-link
              &:after
                content: '\e812'
                font-size: 24px
                line-height: 24px
        .main-pagination
          display: flex
          align-items: center
          justify-content: center
          position: absolute
          left: 0
          bottom: 0
          height: 10px
          width: 100%
          z-index: 1
          @media (min-width: 768px)
            flex-direction: column
            left: calc(100% - 10px)
            height: 100%
            width: 10px
          @media (min-width: 1200px)
            left: calc(100% - 15px)
          .swiper-pagination-bullet
            display: block
            width: 10px
            height: 10px
            border-radius: 50%
            background-color: #D9D9D9
            transition: background-color .3s
            margin: 0 5px
            opacity: 1
            @media (min-width: 768px)
              margin: 5px 0
            &.swiper-pagination-bullet-active
              background-color: $accent-color

/* learning page */
.learn-main
  &__content
    @media (min-width: 768px)
      display: flex
      align-items: flex-start
      justify-content: flex-start
      margin-top: 30px
    @media (min-width: 1200px)
      margin-top: 25px
    &__left-side
      @media (min-width: 768px)
        flex: 0 0 calc(50% + 30px)
        max-width: calc(50% + 30px)
      @media (min-width: 1024px)
        flex: 0 0 60%
        max-width: 60%
      @media (min-width: 1200px)
        flex: 0 0 70%
        max-width: 70%
      .learn-btn
        max-width: 252px
        margin-top: 20px
        font-size: 12px
        padding: 16px
        letter-spacing: .55px
        @media (min-width: 1200px)
          max-width: 220px
    &__right-side
      margin-top: 20px
      @media (min-width: 768px)
        margin-top: -25px
        flex: 0 0 calc(45% - 30px)
        max-width: calc(45% - 30px)
      @media (min-width: 1024px)
        flex: 0 0 35%
        max-width: 35%
      @media (min-width: 1200px)
        flex: 0 0 30%
        max-width: 30%
      .learning-image
        display: block
        width: auto
        max-width: 100%
        margin: 0 auto
        @media (min-width: 1200px)
          width: 125%
          max-width: initial
          position: relative
          top: -40px
          right: 60px








