/* mobile full height fix */
:root {
  --app-height: 100%;
}

* {
  box-sizing: border-box;
  outline: none;
}
*::before, *::after {
  box-sizing: border-box;
}

body,
h1, h2, h3, h4, h5, h6, p,
ul[class], ol[class], li,
figure, figcaption, blockquote,
dl, dd {
  margin: 0;
}

ul[class], ol[class] {
  padding: 0;
  list-style-type: none;
}

a:not([class]) {
  text-decoration-skip-ink: auto;
  color: inherit;
}
a[class] {
  text-decoration: none;
}

input, button, textarea, select {
  font: inherit;
}

button, label {
  cursor: pointer;
}

body {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  min-width: 320px;
  position: relative;
  font-family: DroidSans, sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 21px;
  letter-spacing: 0.45px;
  color: #1C1B1F;
  background-color: #FFF;
  scroll-behavior: smooth;
  text-rendering: optimizeSpeed;
  overflow-x: hidden;
}
body::-webkit-scrollbar {
  width: 6px;
  height: 6px;
  border-radius: 0;
}
body::-webkit-scrollbar-thumb {
  background: #F96791;
  border-radius: 0;
  transition: background-color 0.3s;
}
body::-webkit-scrollbar-thumb:hover {
  background: #6FDE9B;
}
body::-webkit-scrollbar-track {
  background: #dcdcdc;
  border-radius: 0;
}
body {
  scrollbar-face-color: #F96791;
  scrollbar-track-color: #dcdcdc;
}
body.overflow {
  overflow: hidden;
}
body.overflow-menu {
  overflow: hidden;
}
@media (min-width: 640px) and (max-width: 1024px) {
  body.overflow-menu {
    position: relative;
  }
  body.overflow-menu:after {
    content: "";
    display: block;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.7);
    position: fixed;
    top: 0;
    left: 0;
    opacity: 1;
    z-index: 1;
  }
}

.container {
  width: 100%;
  margin: 0 auto;
  max-width: 1140px;
  padding: 0 15px;
}

.header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 20;
  background-color: #fff;
  border-bottom: 1px solid transparent;
  transition: border-color 0.3s;
}
.header__top {
  background: linear-gradient(75.57deg, rgba(250, 97, 115, 0.5) 15.62%, rgba(22, 147, 220, 0.5) 57.16%, rgba(0, 199, 83, 0.5) 94.08%);
}
.header__top .container {
  padding: 20px 15px;
  position: relative;
  transition: padding 0.3s;
}
@media (max-width: 360px) {
  .header__top .container {
    padding: 20px 5px;
  }
}
@media (min-width: 1200px) {
  .header__top .container {
    padding: 16px;
    max-width: 1410px;
  }
}
@media (min-width: 576px) {
  .header__top__mobile {
    text-align: center;
  }
}
